import React from 'react';
import {connect} from 'react-redux';
import vkbridge from '@vkontakte/vk-bridge';
import {closePopout, goBack, openModal, openPopout, setPage} from '../../store/router/actions';
import {Div, Panel, Alert, Group, Button, Cell, Tabs, Search, List, PanelHeader, Input, PanelHeaderBack, Link, Avatar, Separator, Spinner, Snackbar, RichCell, Text, MiniInfoCell, FormLayout} from "@vkontakte/vkui";
import {setActiveTab, setScrollPositionByID} from "../../store/vk/actions";
import Icon20ArticleOutline from '@vkontakte/icons/dist/20/article_outline';
import Icon20FollowersOutline from '@vkontakte/icons/dist/20/followers_outline';
import Icon20WorkOutline from '@vkontakte/icons/dist/20/work_outline';
import Icon20GlobeOutline from '@vkontakte/icons/dist/20/globe_outline';
import Icon20CalendarOutline from '@vkontakte/icons/dist/20/calendar_outline';
import Icon20EducationOutline from '@vkontakte/icons/dist/20/education_outline';
import TabUsers from '../ans/base.js';
import {questionID, questionClosed, questionDate, questionDescription, questionText, questionVKID, questionUserPhoto, questionUsername} from '../ans/base.js';

class HomePanelBase extends React.Component {
    state = {
        showImg: false,
        value1: '',
        answers: [],
        guestions: []
    };
    showImg = () => {
        this.setState({showImg: true});
    };
    onChanges = (e) => {
      const { name, value } = e.currentTarget;
      this.setState({ [name]: value });
      }
    componentDidMount(){
      vkbridge.subscribe((e) => {
        switch(e.detail.type) {
case 'VKWebAppGetUserInfoResult':
this.setState({ fetchedUser: e.detail.data });
break;
}})


fetch(`https://peaceful-oasis-84546.herokuapp.com/api.php?${window.location.search.replace("?", "")}&method=questions.getById&qid=${questionID}`)
.then(res => res.json())
.then(res => {
  this.setState({questions: res.reverse()});
});

      fetch(`https://peaceful-oasis-84546.herokuapp.com/api.php?${window.location.search.replace("?", "")}&method=answers.get&qid=${questionID}`)
      .then(res => res.json())
      .then(res => {
        this.setState({answers: res.reverse()});
      });
    }
    add(){
      if(this.state.value1 === '') return this.setState({
        snackbar:
        <Snackbar
          layout="vertical"
          onClose={() => this.setState({ snackbar: null })}
        >
          Вы не заполнили поле
        </Snackbar>
        });
        if(this.state.value2 === '') return this.setState({
          snackbar:
          <Snackbar
            layout="vertical"
            onClose={() => this.setState({ snackbar: null })}
          >
            Вы не заполнили поле
          </Snackbar>
          });
      fetch(`https://peaceful-oasis-84546.herokuapp.com/api.php?${window.location.search.replace("?", "")}&method=answers.add&qid=${questionID}&text=${this.state.value1}`)
      .then(res => res.json())
      .then(res => {
        if(res.response){
          this.setState({
            snackbar:
            <Snackbar
              layout="vertical"
              onClose={() => this.setState({ snackbar: null })}
            >
            Не все указано
            </Snackbar>
        })
    }else{      
        this.setState({
          answers: res.reverse(),
          value1: '',
          snackbar:
          <Snackbar
            layout="vertical"
            onClose={() => this.setState({ snackbar: null })}
          >
           Вы подали ответ
          </Snackbar>
      })
    }
    })
    }
    render() {
        const {id, setPage, withoutEpic} = this.props;
        return (
            <Panel id={id}>
                <PanelHeader left={<PanelHeaderBack onClick={this.props.goBack}/>}>Вопрос</PanelHeader>
                <Div>
                <Text weight="semibold">{questionDescription}</Text>
                </Div>
                <Group>
                <MiniInfoCell
        before={<Icon20ArticleOutline />}
        multiline
      >
        {questionText}
      </MiniInfoCell>
      <Group>
      <MiniInfoCell
        before={<Icon20FollowersOutline />}
      >
      <Link href={'https://vk.com/id'.questionVKID} target='_blank'>{questionUsername}</Link>
      </MiniInfoCell>

      <MiniInfoCell
        before={<Icon20CalendarOutline/>}
      >
        {questionDate}
      </MiniInfoCell>
    </Group>
    <br/>
    <Separator wide/>
      {this.state.answers != [] && this.state.answers.map((item, index) => <>
    <Group>
    <RichCell
        disabled
        multiline
        before={<Avatar size={48} src={this.state.answers[index]['userphoto']}/>}
        text={this.state.answers[index]['text']}
        caption={this.state.answers[index]['date']}
      >
        <Link href={'https://vk.com/id'.this.state.answers[index]['vkid']} target='_blank'>{this.state.answers[index]['username']}</Link>
      </RichCell>
      </Group>
      <Separator wide/>
      </>
      )}
      <FormLayout>
                      <Input
min="0"
maxLength="900"
value={this.state.value1}
type="text"
name="value1"
placeholder="Ответ"
onChange={this.onChanges}
onKeyDown={this.func}
/>
</FormLayout>
<Div>
                          <Button size="xl" mode="secondary" onClick={() => this.add()}>Отправить</Button>
                          </Div>
      </Group>
      {this.state.snackbar}
</Panel>
        );
    }

}

const mapDispatchToProps = {
    setPage,
    goBack,
    openPopout,
    closePopout,
    openModal
};

export default connect(null, mapDispatchToProps)(HomePanelBase);